<template>
  <div>
    <div v-if="type==='type1'" class="type1">
      <img v-for="(item, index) in data" :key="index" :src="item.url" alt="">
    </div>
    <div v-if="type==='type2'" class="type2">
      <img v-for="(item, index) in data" :key="index" :src="item.url" alt="">
    </div>
    <div v-if="type==='type3'" class="type3">
      <img :src="data[0].url" alt="">
      <div class="right">
        <img :src="item.url" v-for="(item, index) in type3" :key="index" alt="">
      </div>
    </div>
    <div v-if="type==='type4'" class="type4">
      <img :src="data[0].url" alt="">
      <div class="right">
        <img :src="item.url" v-for="(item, index) in type3" :key="index" alt="">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ImgArrangement",
  props: {
    type: {
      type: String,
      default: ""
    },
    data: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    type3() {
      return this.data.slice(1, 3);
    }
  }
};
</script>

<style lang="less" scoped>
  .type1 {
    display: flex;
    justify-content: space-between;
    img {
      width: calc((100% - 20px) / 2);
    }
  }
  .type2 {
    display: flex;
    justify-content: space-between;
    img {
      width: calc((100% - (30px * 2)) / 3);
    }
  }
  .type3 {
    display: flex;
    justify-content: space-between;
    &>img{
      width: 750px;
      margin-right: 30px;
    }
    .right {
      display: flex;
      flex-direction: column;
      flex: 1;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .type4 {
    img {
      width: 100%;
      height: 336px;
    }
  }
</style>
